/**
 * 最外层包裹的home组件，包括
 */
import React, { PureComponent } from 'react';
import BasicLayout from 'Layouts/WorkManage';
import {
  Route, Redirect,
} from 'react-router-dom';
import {
  HistoryPage,ListPage,OperatePage
} from './configs'

class SparepartsRouter extends PureComponent {
  render() {
    return <BasicLayout children={<React.Fragment>
        <Route exact path="/work" render={() => <Redirect to='/work/operate' />} />
        <Route path='/work/operate' component={OperatePage} />
        <Route path='/work/list' component={ListPage} />
        <Route path='/work/history' component={HistoryPage} />
    </React.Fragment>} />;
  }

}
export default SparepartsRouter;