import React, { Component } from 'react';
import { Input, Form, Button, Select, Modal, Table } from 'antd';
import { inject, observer } from 'mobx-react';
const { Option } = Select;

const formItemLayout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 12 },
};
@inject('WorkStore', 'DeviceDefineStore')
@observer
class AddModal extends Component {
  constructor(props) {
    super(props)
    this.store = this.props.WorkStore;
  }
  render() {
    const { getFieldDecorator } = this.props.form;
    const { visible, onClose } = this.props;
    const { currentMain, } = this.store;
    const { employeeList } = this.props.DeviceDefineStore;
    const columns = [
      {
        title: '姓名',
        dataIndex: 'workerName',
        key: 'workerName'
      },
      {
        title: '代接工单数',
        dataIndex: '1',
        key: '1'
      },
      {
        title: '已接待完成数',
        dataIndex: '2',
        key: '2'
      },
    ];
    const data = JSON.stringify(this.store.workDetailList) !== '{}' ? [this.store.workDetailList] : []
    return <Modal
      title={'派单'}
      visible={visible}
      onCancel={onClose}
      footer={
        <React.Fragment>
          <Button onClick={this.props.onClose}>取消</Button>
          <Button type='primary' onClick={this.onOk}>确定</Button>
        </React.Fragment>
      }
    >
      <Form>
        <Form.Item {...formItemLayout} label="工单编号">
          {getFieldDecorator('workNo', {
            initialValue: currentMain.workNo,
            rules: [
              {
                required: true,
                message: '请输入工单编号'
              }
            ]
          })(
            <Input disabled placeholder='请输入' style={{ width: '250px' }} />
          )}
        </Form.Item>

        <Form.Item {...formItemLayout} label="维保人员">
          {getFieldDecorator('workerId', {
            initialValue: currentMain.workerId,
            rules: [
              {
                required: true,
                message: '请输入维保人员'
              }
            ]
          })(
            <Select
              showSearch
              optionFilterProp='children'
              placeholder="请选择"
              onChange={this.getWorkerDetail}
              style={{ width: 250 }}
            >
              {
                employeeList.map(mas => <Option key={mas.id} value={mas.id}>{mas.name}</Option>)
              }
            </Select>
          )}
        </Form.Item>
        <Table columns={columns} size='middle' dataSource={data} pagination={false} />
      </Form>
    </Modal>
  }

  getWorkerDetail = (e) => {
    console.log(e)
    this.props.DeviceDefineStore.employeeList.map(mas => {
      if (e === mas.id) this.store.workerName = mas.name
    })
    this.store.getCountByWorkerId({ workerId: e })
  }
  onOk = () => {
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.store.saveWork({
          id: this.store.currentMain.id,
          ...values,
          workStatus: this.store.currentMain.workStatus.toString() === '0' ? 1 : 7,
          deviceId: this.store.currentMain.deviceId,
        })
        this.props.onClose();
      }
    });
  }
  componentDidMount = () => {
    // this.store.workDetailList = {}
  }
}
const WrappedDynamicRule = Form.create({})(AddModal);
export default WrappedDynamicRule;