import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import {
	Form,
	Input,
	Select,
	Row,
	Col,
	Button,
} from 'antd';
import { workStatus } from 'constants/status_constant'
import { withRouter } from 'react-router-dom';
import { initSearchQuery } from 'utils/dataTools';
const { Option } = Select;

const formItemLayout = {
	labelCol: { span: 6 },
	wrapperCol: { span: 12 }
};

@withRouter
@inject('WorkStore', 'DeviceDefineStore')
@observer
class SearchBar extends Component {
	constructor(props) {
		super(props);
		this.store = this.props.WorkStore;
		this.state = {
			visible: false,
			status: false,
			tags: []
		};
	}

	render() {
		const { getFieldDecorator } = this.props.form;
		const { selectType, customerList, } = this.props.DeviceDefineStore;
		const { searchCondition } = this.store;
		return (
			<div className='list_search'>
				<Form>
					<Row>
						<Col span={12}>
							<Form.Item label='工单编号' {...formItemLayout}>
								{getFieldDecorator('workNo', {
									initialValue: searchCondition.workNo
								})(
									<Input style={{ width: 230 }} allowClear placeholder='输入' />
								)}
							</Form.Item>
						</Col>

						{/*<Col span={8}>
							<Form.Item label='设备类型' {...formItemLayout}>
								{getFieldDecorator('typeId')(
									<Select style={{ width: 230 }} allowClear onChange={this.onSelectType}
										placeholder='请选择设备类型'
										showSearch
										optionFilterProp="children"
										filterOption={(input, option) =>
											option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
										}>
										{selectType.length > 0 &&
											selectType.map(item => (
												<Option key={item.id} value={item.id}>
													{item.typeName}
												</Option>
											))}
									</Select>
								)}
							</Form.Item>
						</Col>
						<Col span={8}>
							<Form.Item label='合作伙伴' {...formItemLayout}>
								{getFieldDecorator('customerId')(
									<Select style={{ width: 230 }} allowClear placeholder='请选择合作伙伴'
										showSearch
										optionFilterProp="children"
										filterOption={(input, option) =>
											option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
										}>
										{customerList.length > 0 &&
											customerList.map(item => (
												<Option key={item.id} value={item.id}>
													{item.customerName}
												</Option>
											))}
									</Select>
								)}
							</Form.Item>
						</Col>*/}
						<Col span={12}>
							<Form.Item label='工单状态' {...formItemLayout}>
								{getFieldDecorator('workStatus')(
									<Select style={{ width: 230 }} allowClear placeholder='请选择工单状态'>
										{
											Object.keys(workStatus).map(item => (
												<Option key={item} value={item}>
													{workStatus[item]}
												</Option>
											))}
									</Select>
								)}
							</Form.Item>
						</Col>
					</Row>
					<Row>
						<Col span={12}>
							<Form.Item label='工单类型' {...formItemLayout}>
								{getFieldDecorator('workType')(
									<Select style={{ width: 230 }} allowClear placeholder='请选择工单类型'>
										<Option value={3}>保养</Option>
										<Option value={4}>故障</Option>
									</Select>
								)}
							</Form.Item>
						</Col>
						<Col span={12}>
							<Form.Item label='工单等级' {...formItemLayout}>
								{getFieldDecorator('level')(
									<Select style={{ width: 230 }} allowClear placeholder='请选择任务等级'>
										<Option value={1}>紧急</Option>
										<Option value={2}>高级</Option>
										<Option value={3}>一般</Option>
									</Select>
								)}
							</Form.Item>
						</Col>
					</Row>
					<Row>
						<Col span={24} style={{ textAlign: 'left' }}>
							<Button
								// size='small'
								style={{ fontSize: '14px' }}
								type='primary'
								onClick={this.handleSearch}
							>
								查询
							</Button>
							<Button
								// size='small'
								style={{ fontSize: '14px', marginLeft: '20px' }}
								onClick={this.handleReset}
							>
								重置
							</Button>
						</Col>
					</Row>
				</Form>
			</div>
		);
	}
	handleSearch = () => {
		let query = {}, workNo = '';
		if (this.props.location.search) {
			query = initSearchQuery(this.props.location.search)
		}
		if (this.props.form.getFieldValue('workNo')) {
			workNo = this.props.form.getFieldValue('workNo')
		}
		this.props.history.push({ search: query.tab ? '?tab=' + query.tab + '&workNo=' + workNo : '' });
		this.props.form.validateFieldsAndScroll((err, values) => {
			if (!err) {
				this.store.searchCondition = values;
				this.store.getWork({});
			}
		});
	};
	handleReset = () => {
		this.props.form.setFieldsValue({
			workCode: undefined,
			typeId: undefined,
			customerId: undefined,
			workStatus: undefined,
			level: undefined,
			workType: undefined
		});
	};

	onSelectType = value => {
		this.props.DeviceDefineStore.getSelectModel({ typeId: value });
	};
	componentDidMount() {
		this.props.DeviceDefineStore.getSelectType({});
		this.props.DeviceDefineStore.getCustomerList({
			customerType: 1
		})
	}
}
const WrapperClass = Form.create({})(SearchBar);
export default WrapperClass;
