import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import GlobalList from 'components/GlobalList';
import AddModal from './AddModal'
import { setWorkColumns, } from '../methods';
import { Modal, Input, Radio, message } from 'antd';
import { initSearchQuery } from 'utils/dataTools';
import { withRouter } from 'react-router-dom'

@withRouter
@inject('WorkStore', 'DeviceDefineStore')
@observer
class DefineTable extends Component {
	constructor(props) {
		super(props);
		this.store = this.props.WorkStore;
		this.state = {
			visible: false,
			selectedRowKeys: [],
			fileVisible: false,
			reason: '',
			solution: '',
			isSolute: 1,
			submitVisible: false,
			submitRecord: {}
		}
	}
	render() {
		const {
			isLoading, workList, workPage, workCols,
		} = this.store;
		const rowSelection = {
			selectedRowKeys: this.store.selectedRowKeys,
			onChange: this.props.handleSelect
		};
		return (
			<div>
				<GlobalList
					table={{
						rowKey: 'id',
						columns: setWorkColumns({
							cols:workCols,
							handleDistribute: this.handleDistribute, handleDetail: this.handleDetail,
							handleReceive: this.handleWorkStatus, handleOver: this.handleWorkStatus,
							handleRecall: this.handleDistribute, handleSubmit: this.handleWorkStatus, handleRefuse: this.handleWorkStatus
						}),
						dataSource: workList,
						pagination: {
							...workPage,
							onChange: (pageIndex, pageSize) => {
								this.store.getWork({
									pageIndex,
									pageSize,
								});
							}
						},
						loading: isLoading,
						rowSelection,
					}}
				/>
				{
					this.state.visible && <AddModal
						visible={this.state.visible}
						onClose={this.onClose}
					/>
				}
				{
					this.state.submitVisible && <Modal
						width={this.state.submitRecord.maintainType === 4 ? 600 : 416}
						title='提示'
						visible={this.state.submitVisible}
						okText='提交'
						cancelText='取消'
						onCancel={this.onClose}
						onOk={this.onSubmitSolute}
					>
						{
							this.state.submitRecord.maintainType === 4 ? <div>
								<div style={{ fontSize: 14, fontWeight: 600 }}>请填写工单完成内容</div>
								<div style={{ margin: '10px 0' }}>
									<span style={{ color: '#F04864' }}>*</span>
									<span>故障原因：</span>
									<Input style={{ width: '80%' }} placeholder='请输入' onChange={this.handleOverInput.bind(this, 'reason')} />
								</div>
								<div>
									<span style={{ color: '#F04864' }}>*</span>
									<span>是否解决：</span>
									<Radio.Group defaultValue={1} onChange={this.handleIsSoluteChange}>
										<Radio value={1}>是</Radio>
										<Radio value={0}>否</Radio>
									</Radio.Group>
								</div>
								<div>
									<span style={{ color: '#F04864' }}>*</span>
									<span>{this.state.isSolute.toString() === '1' ? '解决方案：' : '备注问题：'}</span>
									<Input style={{ width: '80%', margin: '10px 0' }} placeholder='请输入' onChange={this.handleOverInput.bind(this, 'solution')} />
								</div>
							</div> : '确定提交该工单?'
						}
					</Modal>
				}
			</div>
		);
	}
	onSubmitSolute = () => {
		let record = this.state.submitRecord, workStatus = '';
		let data = {
			id: record.id, workStatus: workStatus, deviceId: this.store.currentMain.deviceId,
		};
		if (record.maintainType === 4) {
			if (!this.state.reason || (this.state.isSolute.toString() == 1 && !this.state.solution)) {
				message.warn('请填写相关内容')
			} else {
				data.reason = this.state.reason;
				data.solution = this.state.solution;
				data.workStatus = this.state.isSolute == 1 ? 5 : 6;
				this.store.saveWork({
					...data
				})
				this.onClose()
			}
		} else {
			data.workStatus = 5;
			this.store.saveWork({
				...data
			})
			this.onClose()
		}
	}
	handleWorkStatus = (record, type) => {
		this.store.currentMain = record;
		let content = '', workStatus = '', width = 416;
		switch (type) {
			case 'receive':
				content = '确定接收该工单？';
				workStatus = 2;
				break;
			case 'refuse': content = '确定拒绝该工单？'; workStatus = 3;
				break;
			case 'submit': this.state.submitVisible = true; this.state.submitRecord = record;
				break;
			case 'over': content = '确定完结该工单？'; workStatus = 8;
				break;
			default: break;
		}
		if (!this.state.submitVisible) {
			Modal.confirm({
				title: '提示',
				content,
				width,
				okText: '确定',
				cancelText: '取消',
				onOk: () => {
					let data = {
						id: record.id, workStatus: workStatus, deviceId: this.store.currentMain.deviceId,
					};
					this.store.saveWork({
						...data
					})
				}
			})
		}
		this.setState({})
	}
	handleIsSoluteChange = e => {
		this.setState({
			isSolute: e.target.value
		})
	}
	handleOverInput = (text, e) => {
		this.setState({
			[text]: e.target.value
		})
	}
	handleDistribute = record => {
		this.store.currentMain = record;
		this.store.workerName=record.workerName
		this.store.getCountByWorkerId({ workerId: record.workerId })

		this.setState({
			visible: true
		}, () => {
			this.props.DeviceDefineStore.getUsersByRoles();
		});
	};
	handleDetail = record => {
		this.store.currentMain = record;
		this.store.isDaily = true;
	}
	onClose = () => {
		this.setState({
			visible: false,
			submitVisible: false
		})
	}
	/* modal - operate */
	handleVisible = () => {
		this.store.visible = !this.store.visible;
	};
	/* 弹窗提交操作 */
	handleSubmit = values => {
		const { currentMain } = this.store;
		let params = {
			...values,
			...currentMain
		}
		this.store.saveType(params);
		this.handleVisible();
	};
}
export default DefineTable;
