import React, { Component } from 'react';
import { Input, Form, Button, Select, Modal, Table, message } from 'antd';
import { inject, observer } from 'mobx-react';
const { Option } = Select;

const formItemLayout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 12 },
};
@inject('WorkStore', 'DeviceDefineStore')
@observer
class AddModal extends Component {
  constructor(props) {
    super(props)
    this.store = this.props.WorkStore;
    this.state = {
      selectedRowKeys : []
    }
  }
  render() {
    const { getFieldDecorator } = this.props.form;
    const { visible, onClose } = this.props;
    const { currentMain, } = this.store;
    const { employeeList } = this.props.DeviceDefineStore
    const columns = [
      {
        title: '姓名',
        dataIndex: 'name',
        key: 'name'
      },
      {
        title: '待接工单数',
        dataIndex: 'count',
        key: 'count'
      },
      {
        title: '已接待完成数',
        dataIndex: 'count2',
        key: 'count2'
      },
    ];
    const data = this.store.workDetailList
    return <Modal
      title={'派单'}
      visible={visible}
      onCancel={onClose}
      footer={
        <React.Fragment>
          <Button onClick={this.props.onClose}>取消</Button>
          <Button type='primary' onClick={this.onOk}>确定</Button>
        </React.Fragment>
      }
    >
      <Form>
        <Form.Item {...formItemLayout} label="工单编号">
          {getFieldDecorator('workNo', {
            initialValue: currentMain.workNo,
            rules: [
              {
                required: true,
                message: '请输入工单编号'
              }
            ]
          })(
            <Input disabled placeholder='请输入' style={{ width: '250px' }} />
          )}
        </Form.Item>

        {/* <Form.Item {...formItemLayout} label="维保人员">
          {getFieldDecorator('workerId', {
            initialValue: currentMain.workerId,
            rules: [
              {
                required: true,
                message: '请输入维保人员'
              }
            ]
          })(
            <Select
              showSearch allowClear
              optionFilterProp='children'
              placeholder="请选择维保人员"
              style={{ width: 250 }}
              onChange={this.getWorkerDetail}
            >
              {
                employeeList.map(mas => <Option key={mas.id} value={mas.id}>{mas.name}</Option>)
              }
            </Select>
          )}
        </Form.Item> */}
        <Table 
          columns={columns} 
          size='middle' 
          rowKey='id'
          dataSource={data} 
          pagination={false} 
          locale = {{emptyText:'暂无数据，请选择维保人员'}}
          rowSelection = {{
            selectedRowKeys:this.state.selectedRowKeys,
            onChange:this.onSelectedChange
          }}
        />
      </Form>
    </Modal>
  }
  onSelectedChange = selectedRowKeys =>{
    selectedRowKeys = [selectedRowKeys.pop()]
    this.setState({
      selectedRowKeys
    })
  }
  // getWorkerDetail = (e) => {
  //   console.log(e)
  //   this.props.DeviceDefineStore.employeeList.map(mas => {
  //     if (e === mas.id) this.store.workerName = mas.name
  //   })
  //   this.store.getCountByWorkerId({ workerId: e })
  // }
  onOk = () => {
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        if(this.state.selectedRowKeys.length > 0 ){
          this.store.saveWork({
            id: this.store.currentMain.id,
            ...values,
            workerId:this.state.selectedRowKeys[0],
            workStatus: this.store.currentMain.workStatus.toString() === '0' ? 1 : 7,
            deviceId: this.store.currentMain.deviceId,
          })
          this.props.onClose();
        }else{
          message.error('必须选择一位维保人员')
        }
      }
    });
  }
  componentDidMount() {
    this.store.getCountByWorkerId()
  }
}
const WrappedDynamicRule = Form.create({})(AddModal);
export default WrappedDynamicRule;