import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import Tablelayout from 'components/TableLayout';
import { Modal, Button, Empty } from 'antd';
import { isEmpty } from 'lodash';
import './index.less';

@inject('WorkStore')
@observer
class SearchBar extends Component {
  constructor(props) {
    super(props);
    this.store = this.props.WorkStore;
  }

  render() {
    const { logDetailList } = this.store;
    return (
      <Modal
        title='详情'
        visible={this.props.visible}
        onCancel={this.props.onCancel}
        width={'80%'}
        footer={
          <Button onClick={this.props.onCancel} type='primary'>
            关闭
					</Button>
        }
      >
        <div style={{ marginTop: '20px' }}>
          <div className='breaken-table'>
            {!isEmpty(logDetailList) ? <table border='1' style={{ width: '100%' }}>
              <tr>
                <th>内容</th>
                <th>结果</th>
                <th style={{ borderBottom: 'none', borderRight: 'none' }}>
                  详情
								</th>
              </tr>
              {
                logDetailList.length > 0 && logDetailList.map((item, index) => {
                  return <tr key={index}>
                    <td className='breaken-content'>{item.content}</td>
                    <td>
                      <div className='breaken-result'>
                        {item.contentType === 5 ? item.result.split(',').map(src => (src === 'undefined' ? '未上传图片' : <img src={'http://file.boranet.cn/' + src} onClick={e => window.open('http://file.boranet.cn/' + src)} style={{ width: 100, height: 100, cursor: 'pointer', marginRight: 5 }} alt='图片加载异常' />)) : item.contentType === 6 ? <audio src={'http://file.boranet.cn/' + item.result} controls="controls">
                          您的浏览器不支持音频播放,请升级当前浏览器或换个浏览器试试吧
        </audio> : item.result}
                      </div>
                    </td>
                    <td>
                      <div className='breaken-detail' >
                        {item.resultVOList && item.resultVOList.map(log => (
                          <tr style={{ height: '100%' }}>
                            <td >{log.content}</td>
                            <td className='breaken-result'>{log.contentType === 5 ? log.result.split(',').map(src => (<img src={'http://file.boranet.cn/' + src} onClick={e => window.open('http://file.boranet.cn/' + src)} style={{ width: 50, height: 50, cursor: 'pointer', marginRight: 5 }} alt='图片加载异常' />)) : log.contentType === 6 ? <audio src={'http://file.boranet.cn/' + log.result} controls="controls">
                              您的浏览器不支持音频播放,请升级当前浏览器或换个浏览器试试吧
</audio> : log.result}</td>
                          </tr>
                        ))}
                      </div>
                    </td>
                  </tr>
                })
              }
            </table> : <Empty />
            }
          </div>
        </div>
      </Modal>
    );
  }
  componentWillUnmount() {
    this.store.logDetailList = []
  }
}
export default SearchBar;
